import React from 'react'

const Header = ({handleToggleDarkMode}) => {
    return (
        <div className='header'>
          <h1>NOTE</h1> 
          <button
            onClick={()=>handleToggleDarkMode((previosDarkMode)=>!previosDarkMode)}
            className='save'
          >Toggle Mode</button>
        </div>
    )
}

export default Header
